import React, { Component } from 'react';
import Button from '@mui/material/Button';

type  LoginProvider = {
  provider: string,
  name: string,
  link: string,
}

type LoginBoxState = {
  providers: LoginProvider[]
  isAuthenticated: boolean | null
}

type LoginBoxProperties = {
  baseUrl: string | null,
  children?: React.ReactNode,
}

export class LoginBox extends Component<LoginBoxProperties, LoginBoxState> {
  state: LoginBoxState = {
    providers: [],
    isAuthenticated: null
  }

  async componentDidMount() {
    const res  = await fetch(this.props.baseUrl + '/u/');
    const respBody = await res.json();
    if (!respBody.is_authenticated && respBody.login) {
      const providers  = respBody.login;
      this.setState({providers, isAuthenticated: false})
    }else {
      this.setState({providers: [], isAuthenticated: true})
    }
  }

  render(): React.ReactNode {
    return <div>{
      this.state.providers.map(
        (provider: LoginProvider) => <Button key={provider.provider} variant="contained" href={this.props.baseUrl + provider.link} >  {provider.name}</Button>
      )
      }
      {this.state.isAuthenticated && (
        this.props.children
      )}
    </div>
  }
}

