import React, { Component } from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import AccountCircle from '@mui/icons-material/AccountCircle';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import Link from '@mui/material/Link';

type AppHeaderProperties = {
  baseUrl: string,
}

type AppHeaderState = {
  isLoaded: boolean,
  isAuthenticated: boolean | null,
  username: string | null,
  logoutLink: string | null,
  anchorEl: null | HTMLElement 
}

export class AppHeader extends Component<AppHeaderProperties, AppHeaderState> {
  state: AppHeaderState = {
    isLoaded: false,
    isAuthenticated: null,
    username: null,
    logoutLink: null,
    anchorEl: null
  }
  async componentDidMount() {

    const res  = await fetch(this.props.baseUrl + '/u/');
    const respBody = await res.json();

    if (respBody.is_authenticated) {
      this.setState({
        ...this.state, 
        isAuthenticated: true,
        isLoaded: true,
        username: respBody.user.username,
        logoutLink: respBody.logout
      })
    }else {
      this.setState({
        ...this.state, 
        isLoaded: true
      });
    }
  }
  setAnchorEl(element: null | HTMLElement ) {
    this.setState({
      ...this.state,
      anchorEl: element
    })
  }

  handleMenu(event: React.MouseEvent<HTMLElement>) {
    this.setAnchorEl(event.currentTarget);
  }
  
  handleClose() {
    this.setAnchorEl(null);
  }

  render(): React.ReactNode {
    // return <div>something{this.state.isLoaded} - {this.state.username}</div>
    return  <Box sx={{ flexGrow: 1 }}>
          <AppBar position="static">
            <Toolbar>
              <IconButton
                size="large"
                edge="start"
                color="inherit"
                aria-label="menu"
                sx={{ mr: 2 }}
              >
                <MenuIcon />
               </IconButton>
               <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                 Expenser
               </Typography>
               {this.state.isAuthenticated && (
                <div>
               <IconButton
                size="large"
                aria-label="Logged in as {this.state.username}"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={this.handleMenu.bind(this)}
                color="inherit"
              >
                <AccountCircle />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={this.state.anchorEl}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={Boolean(this.state.anchorEl)}
                onClose={this.handleClose.bind(this)}
              >
                <MenuItem component={Link} href={this.props.baseUrl + this.state.logoutLink}> Logout {this.state.username}</MenuItem>
              </Menu>
              </div>
               )}
            </Toolbar>
          </AppBar>
        </Box>
  }
}
